<template>
    <div>
        <datepicker :events="scheduleEvents" @dateTouched="handleDayChanged"></datepicker>
        <div class="tips">
            日历中，绿色部分为可预约日期，点击选择预约日期
        </div>
        <div class="list">
            <span>
                13:00-13:30
            </span>
            <span>
                可预约
            </span>
        </div>
    </div>
</template>

<script>
import datepicker from '../../components/calendar/calendar.vue'
export default {
    name: "calendar",
    components: {
        datepicker
    },
    data () {
      return {
          scheduleEvents: [{date: '2019-07-10'},{date: '2019-07-11'},{date: '2019-07-12'}],
          curScheduleList: [],
          selectday: '',
          DateList: {}
      }
    },
    methods: {
        handleDayChanged(day) {
            this.curScheduleList = []
            this.selectday = day
            this.curScheduleList.length = 0
            for (let i in this.DateList) {
                if (day == i) {
                    for (let v of this.DateList[i]) {
                        this.curScheduleList.push(v)
                    }
                }
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .tips{
        padding: 13px 16px;
        background-color: #f5f5f5;
        color: #A0A0A0;
        font-weight: bold;
        font-size: 11px;
    }
    .list{
        display: flex;
        justify-content: space-between;
        padding: 23px 16px;
        font-size: 14px;
        color: #5CB851;
        line-height: 20px;
        border-bottom: 1px solid #F5F5F5;
        position: relative;
        .best{
            position: absolute;
            left: 0;
            top: 0;
            width: 30px;
        }
    }
</style>
