<template>
    <div id="calendar">
        <!-- 年份 月份 -->
        <div class="month">
            <ul>
                <li class="arrow" @click="pickPre(currentYear,currentMonth)"><</li>
                <li class="year-month">
                    <span class="choose-year">{{ currentYear }}年</span>
                    <span class="choose-month">{{ currentMonth }}月</span>
                </li>
                <li class="arrow" @click="pickNext(currentYear,currentMonth)">></li>
            </ul>
        </div>
        <!-- 星期 -->
        <ul class="weekdays">
            <li>周一</li>
            <li>周二</li>
            <li>周三</li>
            <li>周四</li>
            <li>周五</li>
            <li>周六</li>
            <li>周日</li>
        </ul>
        <!-- 日期 -->
        <ul class="days">
      <span  v-for="day in days">

        <!--本月-->
        <span v-if="day.getMonth()+1 != currentMonth" class="other-month" @click="pick(day)">
          <li class="daysli">
            {{ day.getDate() }}
          </li>
        </span>
        <span v-else @click="pick(day)">
          <!--今天-->
          <span v-if="day.getFullYear() == new Date().getFullYear() && day.getMonth() == new Date().getMonth() && day.getDate() == new Date().getDate()" class="active">
            <li class="daysli">
              {{ day.getDate() }}
            </li>
          </span>
          <span v-else :class="isEvent(day)" @click="pick(day)">
            <li class="daysli">
                {{ day.getDate() }}
            </li>
          </span>
        </span>
      </span>
        </ul>
    </div>
</template>
<script >
    export default {
        props:{
            events: {
                type: Array,
                required: true,
                default: [],
                validator (events) {
                    let validate = true
                    events.forEach((event, index) => {
                        if (!event.date) {
                            console.error('Vue-Event-Calendar-Error:' + 'Prop events Wrong at index ' + index)
                            validate = false
                        }
                    })
                    return validate
                }
            }
        },
        data() {
            return {
                currentDay: 1,
                currentMonth: 1,
                currentYear: 1970,
                currentWeek: 1,
                days: [],
                curPickDay: ''
            }
        },
        mounted() {
            this.initData(null);
        },
        methods: {
            // 执行日历
            initData: function(cur) {
                var date;
                if (cur) {
                    date = new Date(cur);
                } else {
                    date = new Date();
                }
                this.currentDay = date.getDate();
                this.currentYear = date.getFullYear();
                this.currentMonth = date.getMonth() + 1;
                this.currentWeek = date.getDay(); // 1...6,0
                if (this.currentWeek == 0) {
                    this.currentWeek = 7;
                }
                var str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay);
                this.days.length = 0;
                // 今天是周日，放在第一行第7个位置，前面6个
                for (var i = this.currentWeek - 1; i >= 0; i--) {
                    var d = new Date(str);
                    d.setDate(d.getDate() - i);
                    this.days.push(d);
                }
                for (var i = 1; i <= 35 - this.currentWeek; i++) {
                    var d = new Date(str);
                    d.setDate(d.getDate() + i);
                    this.days.push(d);
                }
            },
            // 当前日期
            pick: function(date) {
                // alert(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()));

                this.curPickDay = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
                this.$emit('dateTouched', this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()))
            },
            // 上一月
            pickPre: function(year, month) {
                //  setDate(0); 上月最后一天
                //  setDate(-1); 上月倒数第二天
                //  setDate(dx) 参数dx为 上月最后一天的前后dx天
                var d = new Date(this.formatDate(year, month, 1));
                d.setDate(0);
                this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
            },
            // 下一月
            pickNext: function(year, month) {
                var d = new Date(this.formatDate(year, month, 1));
                d.setDate(35);
                this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
            },

            // 返回 类似 2016-01-02 格式的字符串
            formatDate (year, month, day) {
                var y = year;
                var m = month;
                if (m < 10) m = "0" + m;
                var d = day;
                if (d < 10) d = "0" + d;
                return y + "-" + m + "-" + d
            },
            isEvent (date) {
                let handledDate = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
                if (this.curPickDay == handledDate) {
                    return 'active'
                }
                console.log( handledDate)
                for (let event of this.events) {
                    if (event.date == handledDate)
                        return 'event-day'
                }
                return ''
            }
        }
    }
</script>

<style scoped>
    ul {
        list-style-type: none;
        padding: 0;
    }
    .days {
        width:100vw;
        background:white;
        overflow:hidden;
    }
    .month ul{
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content:space-between;
        justify-content:space-between;
        -webkit-align-items: center;
        align-items: center;
        padding: 0 16px;
        height: 37px;
    }
    .month ul li{
        display: -webkit-flex;
        display: flex;
    }
    .arrow {
        font-size: 1.5rem;
    }
    .year-month {
        line-height: 25px;
        font-size: 18px;
        color: #666666;
        font-weight: bold;
    }
    /*.choose-year {*/
        /*margin: 0 10px;*/
    /*}*/
    /*.choose-month {*/
        /*margin: 0 10px;*/
    /*}*/
    .weekdays {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        border-top: 1px dotted #666666;
    }
    .weekdays li{
        -webkit-flex-grow: 1;
        flex-grow: 1;
        text-align: center;
        font-size: 11px;
        color: #A0A0A0;
        line-height: 28px;
        height: 28px;
    }
    .days>span{
        float:left;
        min-width: 14.2857%;
        text-align: center;
    }
    .days>span>span>span{
        display:block;
        width: 100%;
        height: 40px;
        margin:0 auto;
        line-height: 40px;
    }
    .daysli{
        height: 40px;
        line-height: 40px;
        font-size: 12px;
        color: #bebebe;
    }
    .event-day{
        display:block;
        width:40px;
    }
    .other-month {
        display: block;
        width: 100%;
        height: 1.06667rem;
        margin: 0 auto;
    }
    .active {
        color: white;
        background:#5CB851;
    }
    .active li{
        color: #ffffff;
    }
    .event-day {
        background:#DEFBB7;
    }
    .event-day li{
        color: #333333;
    }
</style>
